// ** React Imports
import { Fragment, useEffect, useState } from 'react'
import Select from 'react-select'
// ** Reactstrap Imports
import {
  Row,
  Col,
  Card,
  Form,
  Label,
  Input,
  Button,
  CardBody,
  FormFeedback,
  CardText,
  CardHeader
} from 'reactstrap'
import * as yup from "yup"
import { getData, addOrgPreference } from './store/index'
import { yupResolver } from "@hookform/resolvers/yup"
import axios from '@src/configs/axios/axiosConfig'
// ** Third Party Components
import classnames from 'classnames'
import { useForm, Controller } from 'react-hook-form'
import { activeOrganizationid, activeOrganization, orgUserId } from '@src/helper/sassHelper'
import { useDispatch } from 'react-redux'
import toast from 'react-hot-toast'
const activeOrgId = activeOrganizationid()
const activeOrg = activeOrganization()
const userId = orgUserId()
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'
import { Check } from 'react-feather'

const reminderOptions = [{ id: 1, name: 'Every Days' }, { id: 2, name: 'Every 2 Days' }, { id: 3, name: 'Every 3 Days' }, { id: 4, name: 'Every 4 Days' }, { id: 5, name: 'Every 5 Days' }, { id: 6, name: 'Every 6 Days' }, { id: 7, name: 'Never' }]

const GeneralSettings = () => {

  const dispatch = useDispatch()
  const [block, setBlock] = useState(false)
  const [startingNo, setStartingNo] = useState('00001')
  const [prefix, setPrefix] = useState('INV')
  const [signatureFlag, setSignatureFlag] = useState(false)
  const [signatureId, setSignatureId] = useState(null)
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }

  const schema = yup.object().shape({
    organizationId: yup.string().default(activeOrgId),
    invoiceNote: yup.string().required('Please Enter Invoice Note'),
    invoiceReminderInterval: yup.string().nullable(),
    desiredProfitLimit: yup.string().default(''),
    criticalLimit: yup.string().default(''),
    updatedBy: yup.string().default(userId),
    createdBy: yup.string().default(userId)
  })

  const { handleSubmit, formState: { errors }, control, reset } = useForm({
    resolver: yupResolver(schema),
    defaultValues: schema.cast()
  })

  const onSubmit = async data => {
    setBlock(true)
    await dispatch(addOrgPreference(data))
    setBlock(false)
    toast.success('General Setting Updated Succesfully', { position: 'bottom-right' })
  }

  useEffect(async () => {

    const response = await dispatch(getData(activeOrgId))
    const res = response.payload.data
    if (res !== undefined) {
      reset({
        organizationId: activeOrgId,
        invoiceNote: res.invoicenote.replace(/(\\n)/g, "\n"),
        invoiceReminderInterval: parseInt(res.invoicereminderinterval),
        desiredProfitLimit: res.desiredprofitlimit,
        criticalLimit: res.criticallimit,
        updatedBy: userId,
        createdBy: userId
      })
    }

    setSignatureId(activeOrg.signatureimageid)
    setStartingNo(activeOrg?.invoicestartingno ?? '00001')
    setPrefix(activeOrg?.invoiceprefix ?? 'INV')
    setSignatureFlag(activeOrg?.signatureflag ?? false)

  }, [])

  const getRow = (fieldLabel, fieldName, reqflag = true) => {
    return (
      <Col md={10} className='d-flex mt-2'>
        <Label md='3' className={classnames(`form-label ${reqflag ? 'required' : ''}`)} for={fieldName}>
          {fieldLabel}
        </Label>
        <Col md='7' >
          <Controller
            id={fieldName}
            name={fieldName}
            control={control}
            render={({ field }) => <Input type={fieldName === 'invoiceNote' ? 'textarea' : 'text'} invalid={errors[fieldName] && true} {...field} />}
          />
          {errors[fieldName] && <FormFeedback>{errors[fieldName].message}</FormFeedback>}
        </Col>
      </Col>
    )
  }

  const getSelectRow = (fieldLabel, fieldName, options, reqflag = false) => {
    return (

      <Col md={10} className='d-flex mt-2'>
        <Label md='3' className={classnames(`form-label ${reqflag ? 'required' : ''}`)} for={fieldName} >
          {fieldLabel}
        </Label>
        <Col md='7'>
          <Controller
            control={control}
            name={fieldName}
            id={fieldName}
            render={({ field, ref }) => (
              <Select
                inputRef={ref}
                className={classnames('react-select', { 'is-invalid': errors[fieldName] })}
                {...field}
                classNamePrefix='select'
                options={options}
                value={options.find(c => { return c.id === field.value })}
                onChange={val => { return field.onChange(val.id) }}
                getOptionLabel={(option) => option.name}
                getOptionValue={(option) => option.id}
              />
            )}

          />
          {errors[fieldName] && <FormFeedback className='text-danger'>{errors[fieldName]?.message}</FormFeedback>}
        </Col>
      </Col>

    )
  }

  const uploadFiles = (file) => {

    const formData = new FormData()
    formData.append(`files[0]`, file)
    formData.append('organizationId', activeOrgId)
    formData.append('createdBy', userId)

    setBlock(true)
    axios.post('/google/signatureupload', formData).then((res) => {
      setBlock(false)
      toast.success('Signature Uploaded Successfully')
      setSignatureId(res.data.attachment_id)
    }).catch((err) => { console.log(err) })
  }

  const onChange = e => {
    const reader = new FileReader(),
      files = e.target.files
    reader.readAsDataURL(files[0])

    uploadFiles(e.target.files[0])

  }

  const setPrefixValue = (e) => {
    const re = /(^$)|(^[a-zA-Z]+$)/
    if (re.test(e.target.value)) {
      setPrefix(e.target.value)
    }
  }

  const setStartingNoValue = (e) => {
    const re = /(^$)|(^[0-9]+$)/
    const val = String(parseInt(e.target.value === '' ? 0 : e.target.value))
    if (re.test(val)) {
      setStartingNo(val.padStart(5, '0'))
    }
  }

  const updateSignature = () => {
    setBlock(true)
    const data = {
      signatureFlag,
      prefix,
      startingNo
    }
    axios.post('/organizations/updateinvoicesetting', data).then(() => {
      setBlock(false)
      toast.success('Invoice setting done Successfully')

    }).catch((err) => { console.log(err) })
  }

  return (
    <Fragment>
      <UILoader blocking={block} loader={<Loader />}>
        <Card>
          <CardBody className='py-1'>
            <Row className='col-lg-7 col-sm-12'>
              <h4 style={{ fontWeight: '900' }}>Invoice No</h4>
              <p>Your invoice numbers are set on auto-generate mode to save your time. </p>
              <Row className='col-12'>
                <Col>
                  <CardText>Prefix</CardText>
                  <Input type='text' value={prefix} onChange={setPrefixValue} />
                </Col>
                <Col>
                  <CardText>Next Number</CardText>
                  <Input type='text' value={startingNo} onChange={setStartingNoValue} />
                </Col>
              </Row>
              <Row className='col-12 mt-1'>
                <Col>
                  <CardText>Invoice starting number</CardText>
                  <Input type='text' value={`${prefix}- ${startingNo}`} readOnly />
                </Col>
              </Row>
            </Row>

            <Row className='col-lg-6 col-sm-12 mt-2'>
              <h4 style={{ fontWeight: '900' }}>Signature</h4>

              <div className='d-flex my-1 align-items-center'>Add signature in the invoice.
                <div className='form-switch form-check-primary ms-1'>
                  {signatureFlag ? <Input type='switch' id='switch-priamry' defaultChecked onChange={(e) => { setSignatureFlag(e.target.checked) }} /> : <Input type='switch' id='switch-priamry' onChange={(e) => { setSignatureFlag(e.target.checked) }} />}
                </div>
                {(signatureId && signatureFlag) && <img className='img-fluid width-15-per ms-2' referrerPolicy="no-referrer" src={`https://drive.google.com/thumbnail?id=${signatureId}&authuser=0`} />}
              </div>

              <Row className='col-12'>
                <Col>
                  <Button tag={Label} className='mb-75 me-75' size='sm' color='primary'>
                    Upload Signature
                    <Input type='file' onChange={onChange} hidden accept='image/*' />
                  </Button>
                </Col>
              </Row>
            </Row>
            <Col className='pb-1'>
              <Button color='primary' size='sm' className='mt-2' onClick={() => { updateSignature() }}> <Check size={16} /> Submit</Button>
            </Col>
          </CardBody>
        </Card>
        <Card>
          <h4 className='m-2'>Invoice Notes:</h4>
          <CardBody>
            <Row className=''>
              <Col lg='12'>
                <Row tag={Form} className='gx-2 gy-1' onSubmit={handleSubmit(onSubmit)}>

                  <Row>
                    {getRow('Default invoiceNote', 'invoiceNote', false)}

                    {getSelectRow('Set automatic Reminder', 'invoiceReminderInterval', reminderOptions, false)}

                  </Row>

                  <Col className='mt-2 pt-1' xs={12}>
                    <Button type='submit' className='me-1' color='primary'>
                      Submit
                    </Button>
                    <Button color='secondary' outline>
                      Cancel
                    </Button>
                  </Col>

                </Row>
              </Col>
            </Row>
          </CardBody>
        </Card>
      </UILoader>
    </Fragment>
  )
}

export default GeneralSettings
